<template>
  <div class="def">
    <table-lite
      :is-loading="table.isLoading"
      :columns="table.columns"
      :rows="table.rows"
      :total="table.totalRecordCount"
      @do-search="doSearch"
      @is-finished="table.isLoading = false"
    ></table-lite>
  </div>
</template>

<script>
import { defineComponent, reactive } from "vue";

// import TableLite from "vue3-table-lite";
import TableLite from "./TableLite.vue";
// Fake Data for 'asc' sortable
const sampleData1 = (rows, offst, limit) => {
  // offst = offst+1;
  let data = [];
  for (let i = offst; i <= limit; i++) {
    data.push(rows[i]);
  }
  return data;
};

export default defineComponent({
  name: "default-table",
  components: { TableLite },
  props: {
    columns: {
      type: Array,
      default: () => {
        return [];
      },
    },
    rows: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  setup(props) {
    let width = 100 / props.columns.length;
    let newColumns = [];
    for (let i = 0; i < props.columns.length; i++) {
      newColumns.push({
        label: props.columns[i],
        field: props.columns[i],
        width: width + "%",
      });
    }
    // Table config
    const table = reactive({
      isLoading: false,
      columns: newColumns,
      rows: props.rows,
      totalRecordCount: 0,
    });
    /**
     * Search Event
     */
    const doSearch = (offset, limit) => {
      table.isLoading = true;
      setTimeout(() => {
        // console.log(offset,limit)
        table.isReSearch = offset == undefined ? true : false;
        if (offset >= 10 || limit >= 20) {
          limit = 20;
        }
        table.rows = sampleData1(this.props.rows, offset, limit);
        table.totalRecordCount = 25;
      }, 600);
    };
    // First get data
    doSearch(table.columns, 0, 10);
    return {
      table,
      doSearch,
    };
  },
});
</script>

<style scoped>
.def {
  margin-top: 200px;
  margin-left: 5%;
 left: 0%;
  position: absolute;
width:90%;
}
