<template>
  <h1>Информация о проведенной проверке</h1>
  <table-lite :columns="this.columns" :rows="this.rows"></table-lite>
</template>

<script>
import TableLite from './TableLite.vue';

export default {
  components: { TableLite },
  name: "my-table",
  components: [TableLite],
  props: {
    rows: { Array },
    // columns: { Array },
    info: { String },
  },
  data() {
    return {
      columns: [
        {
          label: "ID",
          field: "id",
          width: "3%",
          sortable: true,
          isKey: true,
        },
        {
          label: "Name",
          field: "name",
          width: "10%",
          sortable: true,
        },
        {
          label: "Email",
          field: "email",
          width: "15%",
          sortable: true,
        },
      ],
    };
  },
};
</script>

