<template>
  <div class="container">
    <h1>Информация о проведенной проверке</h1>
    <h3>Sql = {{ this.item.sqlName }}</h3>
    <h3>Перечень = {{ this.item.pName }}</h3>
    <h3>Data = {{ this.item.date }}</h3>

    <default-mode :rows="this.item.rows" :columns="this.item.columns"/>
  </div>
</template>

<script>
import MyTable from "@/components/Table.vue";
import DefaultMode from "../components/DefaultMode.vue";
export default {
  components: { MyTable, DefaultMode },
  data() {
    return {
      item: {},
    };
  },
  created() {
    let ind = this.$store.state.history.findIndex(
      (i) => i.id === this.$route.params.id
    );
    this.item = this.$store.state.history[ind];
  },
};
</script>

<style>
.container {
  margin: auto;
  display: flex;
  flex-direction: column;
 
}
.container h3 {
  margin: 5px;
  border: 1px solid red;
}
</style>